.site-header {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 3em;
  order: -1;
  background-image: url(././media/Forknblog-top1.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
  z-index: 100;
}

.site-content {
  display: flex;
  flex-direction: row;
  flex: 1 0 auto;
  background-color: white;
  z-index: 10;
}



.single-wrapper {

  z-index: 1;
  background-color: white;
}

.single-page {
  width: 100% !important;

}

.single-project-wrapper {
  width: 90%;
}

.home-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  height: 100%;


}

.about-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.home-header {
  width: 100%;
  order: -1;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 30%;
  margin-bottom: 3em;
  background-image: url(././media/Forknblog-bottom.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  flex-shrink: 0;
  background-position: center;
}

.home-intro {
  width: 90%;
}

.userbar-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-content: space-between;
  font-family: myfontfamily2;
  font-size: x-large;
  height: 2.5em;
  padding-right: .25em;

}

.userbar-username {
  background-color: white;
  padding: .25em;
}

.userbar-image {
  border-radius:10%;
  height:auto;
  width:2.75em;
}

#file {
  pointer-events:none;
}

#readme {
  width: 90%;
  margin: auto;
  overflow-wrap: break-word;
}

.project-box-wrapper {
  display: flex;
  flex-direction: column;
  margin-left: 2em;
  margin-top: 1em;
}

.project-box {
  display: flex;
  width: 90%;
  justify-content: space-between;
}

.project-box-key {
  font-weight: bold;
}

.preview > #file {
  margin-top: -50%;
}

.swiper-slide > #file {
  margin-top: -40%;
}

.single-page > #file {
  margin-top: -40%;
}

.home-intro > #file {
  margin-top: initial;
}

article > div {
  width: 90%;
  margin: auto;
}

.main-wrapper {
  height: 100%;
  width: 100%;

  
}

.main-wrapper.multpage-wrapper.single-wrapper.instapaper_body {
  pointer-events:none;
  margin-top: -7.5em;
}

.multpage-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  background-color: white;
  margin-top: 2em;
}

.article-preview-title {
  display: flex;
  width: 80%;
  justify-content: center;
  flex-wrap: wrap;
  padding-top: 1em;
}

.article-preview-title-word {
  background-color:black;
  color:white;
  margin-right: .25em;
  font-size: x-large;
  padding: .1em;
  z-index: 1;
}

.swiper-slide > .article-preview-title > .article-preview-title-word {
  font-size: large;
}





.main-filter {
  width: 100%;
  height: 3rem;
  display: flex !important;
  justify-content: space-around;
  margin-top: 2.5rem;
}

.menu-link {
  flex: 0 0 auto; 
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}


.menu-selected {
  --color: darkgoldenrod;
  background-image: linear-gradient(var(--color) 0%, var(--color) 100%);
  background-repeat: repeat-x;
  background-position: bottom;
  background-size: 2px 5px;
}




.menu-label {
  font-size: large;
  font-family: myfontfamily;
}

.section-header {
  align-self: baseline;
  font-family: myfontfamily;
  margin-top: 2em;
  margin-bottom: 1em;
  margin-left: 1em;
  font-size: large;
}

.section-header-word {
  font-size: x-large;
}

.swiper-container {
  height: 15em;
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  width: 80%;
  overflow: visible !important;
}

.preview-swiper {
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  height: 80% !important;
  margin-top: 1em;
  overflow: hidden;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.articleswiper {
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  height: 80% !important;
  background-color: white;
  margin-top: 1em;
}

.projectswiper {
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  height: 80% !important;
  background-color: white;
  margin-top: 1em;
  overflow: hidden;
}

.preview-title {
  font: 400 14px Roboto-Regular,HelveticaNeue,Arial,sans-serif;
  font-family: Roboto-Medium,HelveticaNeue-Medium,HelveticaNeue,sans-serif-medium,Arial,sans-serif;
  font-size: 16px;
  padding-bottom: 8px;
  color: rgba(0,0,0,.87);
  line-height: 20px;
  align-self: center;
  width: 80%;
}

.preview-body {
  color: rgba(0,0,0,.87);
  font-family: Roboto,HelveticaNeue,Arial,sans-serif !important;
  font-weight: inherit;
  font-size: 14px;
  line-height: 20px;
  font: 400 14px Roboto-Regular,HelveticaNeue,Arial,sans-serif;
  overflow: hidden;
  max-height: 7em;
  width: 80%;
  align-self: center;
}


.preview-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: white;
}

.preview-stats {
  width: 80%;
  align-self: center;
  display: flex;
  justify-content: space-between;
}

