.logo-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Modal Content (Image) */
.modal-content {
  margin: auto;
  display: block;
  width: 100%;
}

/* Add Animation - Zoom in the Modal */
.modal-content, #caption { 
  animation-name: zoom;
  animation-duration: 0.6s;
}

@keyframes zoom {
  from {transform:scale(0)} 
  to {transform:scale(1)}
}



/* 100% Image Width on Smaller Screens */
@media only screen and (max-width: 700px){
  .modal-content {
    width: 100%;
  }
}

@-moz-keyframes refreshing-loader {
  0% {
    -moz-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-webkit-keyframes refreshing-loader {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes refreshing-loader {
  0% {
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
/* :not(:required) hides this rule from IE9 and below */
.refreshing-loader:not(:required) {
  -moz-animation: refreshing-loader 1000ms infinite linear;
  -webkit-animation: refreshing-loader 1000ms infinite linear;
  animation: refreshing-loader 1000ms infinite linear;
  -moz-border-radius: 2.4em;
  -webkit-border-radius: 2.4em;
  border-radius: 2.4em;
  border: 0.4em solid #9ac;
  border-left-color: transparent;
  color: transparent;
  display: inline-block;
  font-size: 10px;
  line-height: 1.2;
  width: 3em;
  height: 3em;
  text-indent: 100%;
  margin-top: 10em;
}
.refreshing-loader:not(:required):after {
  display: block;
  border: 0.5em solid transparent;
  border-top-color: #9ac;
  border-left-color: #9ac;
  content: '';
  width: 0;
  height: 0;
  overflow: hidden;
  margin-left: -0.2em;
  margin-top: 1em;
}

/* extra small devices (ie. portrait phones)
   no media query since this is the default */


/* small devices (ie. landscape phones) */
@media all and (min-width: 576px) {
  body:after {
    content: "4";
    display: none; 
  }

  .header-menu {
    .header-menu {
      background-size: 30%;
    }

  }
  


}

/* medium devices (ie. tablets) */
@media all and (min-width: 768px) {
  body:after {
    content: "6" !important;
    display: none; }

  .mini-article .article-stats-bar {
    display: flex;
  }

  .mini-article {
      width: 22rem;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      align-items: stretch;
      border-bottom: none;
      margin-top: 2rem;
  }

  .mini-article:hover {
      box-shadow: 0 4px 8px 0 #2F4F4F, 0 6px 20px 0 #2F4F4F;
      transition: all 200ms ease-out;
  }

  .logo {
    display: flex;
  }

  .site-header {
    height: 4.4em;
    width: 100%;
    position: fixed;

  }

  .sitelogo {
    width: 3em;
  }

  .home-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;

  }

 
.main-wrapper {
  margin-top: 4.4em;
}
 

  .userbar-image {
    width: 4em;
  }

  .header-menu {
    background-size: 25%;
    max-height: 4.4em;
  }

  .menu-label {
    font-size: 1.3em;
  }

 

  .site-body {
    flex-direction: row;
    justify-content: center;

    
  }

  .swiper-slide > #file {
    margin-top: -50%;
  }

  .preview > #file {
    margin-top: -55%;
  }
}

/* large devices (ie. desktops) */
@media all and (min-width: 992px) {
  body:after {
    content: "8" !important;
    display: none; }

    .preview > #file {
      margin-top: -60%;
    }


}

/* x large devices (ie. large desktops) */
@media all and (min-width: 1200px) {
  body:after {
    content: "8" !important;
    display: none; } 

  .main-wrapper {
    width: 80%;
  }

  }

  /* x large devices (ie. large desktops) */
@media all and (min-width: 1440px) {
  body:after {
    content: "8" !important;
    display: none; } 

    .main-wrapper {
      width: 70%;
    }

  }

/* xxl large devices (ie. tv) */
@media all and (min-width: 1920px) {
  body:after {
    content: "16" !important;
    display: none; }
  
  }